<!-- src/components/FilterSortApartmentForm.vue -->
<template>
  <div class="operation_sort">
    <!-- Динамическая форма для ввода параметров запроса -->
    <div v-for="(param, index) in queryParams" :key="index" class="param-row">
      <select v-model="param.key" class="param-key">
        <option disabled value="">Выберите параметр</option>
        <option value="sort">Сортировка (sort)</option>
        <option value="filter">Фильтрация (filter)</option>
        <option value="page">Номер страницы (page)</option>
        <option value="size">Размер страницы (size)</option>
      </select>
      <input v-model="param.value" placeholder="Введите значение" class="param-value" />
      <button @click="removeParam(index)" class="remove-button">-</button>
      <span v-if="param.error" class="error">{{ param.error }}</span>
    </div>

    <!-- Кнопка добавления нового параметра -->
    <button @click="addParam" class="add-button">+</button>

    <!-- Кнопка отправки запроса -->
    <button @click="fetchFlats" class="fetch-button">
      Получить список квартир
    </button>

    <!-- Список квартир -->
    <div v-if="flats && flats.length" class="apartments-list">
      <h3>Список квартир:</h3>
      <div v-for="flat in flats" :key="flat.id">
        <table>
          <tbody>
          <tr><td><strong>ID:</strong></td><td>{{ flat.id }}</td></tr>
          <tr><td><strong>Название:</strong></td><td>{{ flat.name }}</td></tr>
          <tr><td><strong>Координаты:</strong></td><td>X: {{ flat.coordinates.x }}, Y: {{ flat.coordinates.y }}</td></tr>
          <tr><td><strong>Площадь:</strong></td><td>{{ flat.area }} м²</td></tr>
          <tr><td><strong>Кол-во комнат:</strong></td><td>{{ flat.numberOfRooms }}</td></tr>
          <tr><td><strong>Отделка:</strong></td><td>{{ flat.furnish }}</td></tr>
          <tr><td><strong>Вид:</strong></td><td>{{ flat.view }}</td></tr>
          <tr><td><strong>Транспорт:</strong></td><td>{{ flat.transport }}</td></tr>
          <tr><td><strong>Дом:</strong></td>
            <td>
              {{ flat.house.name }}, Год: {{ flat.house.year }},
              Этажи: {{ flat.house.numberOfFlatsOnFloor }},
              Лифты: {{ flat.house.numberOfLifts }}
            </td>
          </tr>
          <tr><td><strong>Цена:</strong></td><td>{{ formatPrice(flat.price) }} ₽</td></tr>
          <tr><td><strong>Время до метро пешком:</strong></td><td>{{ flat.timeToMetroByFoot }} мин</td></tr>
          <tr><td><strong>Время до метро на транспорте:</strong></td><td>{{ flat.timeToMetroByTransport }} мин</td></tr>
          </tbody>
        </table>
      </div>
    </div>
<!--    <p v-else-if="flats && flats.length === 0" class="no-results">-->
<!--      Квартиры не найдены.-->
<!--    </p>-->
    <p v-if="errorMessage" class="error-message">{{ errorMessage }}</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flats: [],
      queryParams: [{ key: "", value: "", error: "" }],
      errorMessage: ''
    };
  },
  methods: {
    addParam() {
      this.queryParams.push({ key: "", value: "", error: "" });
    },
    removeParam(index) {
      this.queryParams.splice(index, 1);
    },
    async fetchFlats() {
      this.errorMessage = '';
      this.flats = [];
      const url = new URL("https://localhost:8443/api/v1/flats");
      let hasErrors = false;

      this.queryParams.forEach(param => {
        // Проверка для page и size: должно быть положительным числом
        if (param.key === "page" || param.key === "size") {
          if (!param.value || isNaN(param.value) || Number(param.value) <= 0) {
            param.error = 'Введите положительное число';
            hasErrors = true;
          } else {
            param.error = '';
            url.searchParams.append(param.key, param.value);
          }
        }
        // Проверка для sort
        else if (param.key === "sort") {
          const match = param.value.match(/^<?\s*(id|name|coordinates|area|numberOfRooms|furnish|view|transport|house|price|timeToMetroByFoot|timeToMetroByTransport)$/);
          if (!match) {
            param.error = 'Неверный имя столбца';
            hasErrors = true;
          } else {
            param.error = '';
            url.searchParams.append(param.key, param.value);
          }
        }
        // Проверка для filter
        else if (param.key === "filter") {
          const filterPattern = /^(id|name|coordinates|area|numberOfRooms|furnish|view|transport|house|price|timeToMetroByFoot|timeToMetroByTransport)(=|!=|>|<|>=|<=).+$/;
          if (!filterPattern.test(param.value)) {
            param.error = 'Неправильный формат фильтрации. Пример: filter=id=3';
            hasErrors = true;
          } else {
            param.error = '';
            url.searchParams.append(param.key, param.value);
          }
        } else {
          param.error = 'Параметр обязателен для выбора';
          hasErrors = true;
        }
      });

      if (hasErrors) {
        this.errorMessage = 'Пожалуйста, исправьте ошибки в параметрах.';
        return;
      }

      try {
        const response = await fetch(url.toString(), {
          method: 'GET',
          headers: { 'Accept': 'application/xml' }
        });

        if (response.ok) {
          const xmlData = await response.text();
          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(xmlData, "application/xml");

          const flatNodes = xmlDoc.getElementsByTagName("content");
          if (flatNodes.length === 0) {
            this.errorMessage = "Нет таких квартир для заданных фильтров.";
          } else {
            this.flats = this.parseFlatsFromXML(xmlDoc);
          }
        } else {
          const xmlData = await response.text();
          const parser = new DOMParser();
          const xmlDoc = parser.parseFromString(xmlData, 'application/xml');
          const messageNode = xmlDoc.querySelector('ErrorDefault > message');
          this.errorMessage = messageNode ? messageNode.textContent : 'Произошла ошибка при обработке запроса.';
        }
      } catch (error) {
        this.errorMessage = 'Сервер не отвечает';
      }
    },
    parseFlatsFromXML(xmlDoc) {
      const flatNodes = xmlDoc.getElementsByTagName("content");

      const flats = Array.from(flatNodes).map(flatNode => ({
        id: flatNode.querySelector("id")?.textContent,
        name: flatNode.querySelector("name")?.textContent,
        coordinates: {
          x: flatNode.querySelector("coordinates > x")?.textContent,
          y: flatNode.querySelector("coordinates > y")?.textContent,
        },
        area: flatNode.querySelector("area")?.textContent,
        numberOfRooms: flatNode.querySelector("numberOfRooms")?.textContent,
        furnish: flatNode.querySelector("furnish")?.textContent,
        view: flatNode.querySelector("view")?.textContent,
        transport: flatNode.querySelector("transport")?.textContent,
        house: {
          name: flatNode.querySelector("house > name")?.textContent,
          year: flatNode.querySelector("house > year")?.textContent,
          numberOfFlatsOnFloor: flatNode.querySelector("house > numberOfFlatsOnFloor")?.textContent,
          numberOfLifts: flatNode.querySelector("house > numberOfLifts")?.textContent
        },
        price: flatNode.querySelector("price")?.textContent,
        timeToMetroByFoot: flatNode.querySelector("timeToMetroByFoot")?.textContent,
        timeToMetroByTransport: flatNode.querySelector("timeToMetroByTransport")?.textContent
      }));

      return Array.from(new Map(flats.map(flat => [flat.id, flat])).values());
    },
    formatPrice(price) {
      return Number(price).toLocaleString('ru-RU', { minimumFractionDigits: 0 });
    },
  }
}
</script>

<style scoped>
.operation_sort {
  max-width: 600px;
  margin: 0 auto;
  padding: 20px;
  font-family: Arial, sans-serif;
  background-color: #f0f4ff;
  border-radius: 8px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
}

.param-row {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.param-key, .param-value {
  padding: 8px;
  font-size: 14px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
}

.param-key {
  margin-right: 5px;
  width: 45%;
}

.param-value {
  margin-right: 5px;
  width: 45%;
}

.remove-button {
  background-color: #d81717;
  width: 30px;
  color: white;
  padding: 8px;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.remove-button:hover {
  background-color: #e04343;
}

.add-button {
  background-color: #12e1d0;
  color: white;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-top: 10px;
  display: block;
  width: 100%;
}

.add-button:hover {
  background-color: #210dd3;
}

.fetch-button {
  background-color: #5310da;
  color: white;
  padding: 12px 20px;
  font-size: 16px;
  font-weight: bold;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  width: 100%;
  margin-top: 15px;
}

.fetch-button:hover {
  background-color: #3307a3;
}

.apartments-list {
  margin-top: 20px;
}

.error-message {
  color: #ff4d4f;
  font-weight: bold;
  margin-top: 10px;
}
</style>
